/* Extra large devices (desktops, less than 1280px) */
@media (max-width: 1722px){
	body{
		font-size: 12px;
	}

	h2, h3, h4{
		font-size: 16px;
	}
	
	body .container{
		width: 1200px;
	}

	.logo{
		width: 122px;
	}

	.offer__title{
		font-size: 50px;
	}

	.offer__subtitle{
		font-size: 20px;
	}

	.offer__btn{
		height: 40px;
		padding: 0 38px;
		font-size: 14px;
	}
}

/* Large devices (desktops, less than 1280px) */
@media (max-width: 1280px){
	body .container{
		width: 100%;
		padding: 0 5%;
	}
}

/* Medium devices (tablets, less than 992px) */
@media (max-width: 992px){
	.offer__title{
		font-size: 65px;
	}

	.offer__subtitle{
		font-size: 25px;
	}
}

/* Small devices (landscape phones, less than 768px) */
@media (max-width: 768px){
	body .container{
		padding: 0 5%;
	}
	.offer__title{
		font-size: 45px;
	}
}

/* Extra small devices (portrait phones, less than 576px) */
@media(max-width: 576px){
	body{
		font-size: 9px;
	}
	#header{
		padding-top: 19px;
	}
	.logo{
		width: 110px;
	}
	.nav, .lang{
		display: none;
	}

	.menu_bars{
		display: block;
	}

	.offer__title{
		font-size: 30px;
	}

	.offer__subtitle{
		font-size: 16px;
		line-height: 20px;
	}

	.offer__btn{
		font-size: 12px;
		height: 33px;
		padding: 0 26px;
	}
}