.heading{
  align-items: center;
}

.nav{
  margin-left: 76px;
}

.nav .nav__menu li {
  margin-right: 69px;
}

.nav .nav__menu li:last-child{
  margin-right: 0;
}

.lang{
  margin-left: auto;
}

.lang a,
.nav .nav__menu li a {
  text-transform: uppercase;
  transition: all .3s ease;
}

.lang .lang__cur,
.lang a:hover,
.nav .nav__menu li a:hover{
  color: $main_color;
}