// Large devices (desktops, less than 1200px)
@media (max-width: 1280px) {
	section .container{
		width: 890px;
		padding: 0;
	}

	.section__title{
		font-size: 20px;
		line-height: 29px;
	}
	
	// Hero
	.header{
		padding-bottom: 193px;

		.offer{
			margin-top: 234px;

			&__title{
				font-size: 45px;
			}
		}
	}
	
	// Story
	.story{
		padding-top: 144px;

		.story__article{
			padding: 81px 107px 81px 195px;
			margin-right: 0;

			.story__thumbnail{
				height: 72.5%;
				left: -150px;
			}
		}
	}

	// Facts
	.facts{
		padding-top: 230px;		

		.facts__year{
			width: 103%;
			left: 50%;
			transform: translateX(-50%);
		}	

		.facts__content{
			margin-left: 67px;

			.facts__title{
				width: 223px;
				margin-bottom: 33px;
			}
		}
	}

	// History
	.authors{
		padding-top: 271px;
		padding-bottom: 144px;

		&__text{
			width: 364px;	
			margin-right: 101px;
		}

		.thumbnail{
			width: 50%;
		}
	}
}

// Medium devices (tablets, less than 992px)
@media (max-width: 992px) {
	section .container{
		width: 740px;
	}

	.row{
		align-items: flex-start;
	}

	.section__title{
		font-size: 25px;
		line-height: 37px;
	}

	.year{
		font-size: 60px;
		line-height: 73px;
	}
	
	// Hero
	.header{
		padding-bottom: 636px;

		.offer{
			margin-top: 408px;

			&__title{
				font-size: 65px;
			}
		}
	}
	
	// Story
	.story{
		.story__article{

			.story__year{
    			bottom: -37px;
			}
		}
	}

	// Facts
	.facts{
		.facts__year{
			bottom: -28px;
		}	
	}

	// History
	.authors{
		.row > div{
			width: 50%;
		}

		.authors__text{
			margin-right: 86px;
		}

		.thumbnail{
			width: 300px;
		}
	}
}

// Small devices (landscape phones, less than 768px)
@media (max-width: 768px) {
	section > .container{
		width: 100%;
		padding: 0  5%;
	}

	.year{
		font-size: 40px;
		line-height: 61px;
	}

	.section__title{
		font-size: 16px;
		line-height: 23px;
	}

	.section__text{
		font-size: 9px;
		line-height: 16px;
	}

	// Hero
	.header{
		padding-bottom: 109px;

		.nav, .lang{
			display: none;
		}

		.menu_bars{
			display: block;
		}

		.offer{
			margin-top: 100px;
			width: 255px;

			&__title{
				font-size: 30px;
				margin-bottom: 26px;
			}

			&__subtitle{
				font-size: 16px;
			}
		}
	}

	.story{
		padding-top: 63px;

		.story__article{
			padding: 47px 24px 47px 160px;
			margin-right: 0;

			.story__thumbnail{
				height: 72.5%;
				left: -125px;
			}
		}
	}

	.facts{
		padding-top: 147px;
	}

	.authors{
		padding: 146px 0 168px 0;

		.authors__text{
			margin-right: 42px;
			width: auto;
		}

		.authors__thumbnail{
			width: 50%;
		}
	}
}

// Extra small devices (portrait phones, less than 576px)
@media (max-width: 576px) {
	.row{
		flex-direction: column;
	}

	.thumbnail{
		height: 150px;
		width: 100%;
		object-fit: cover;
	}

	.story > .container{
		padding: 0;
	}

	.story .story__thumbnail{
		width: 163px;
    	object-fit: cover;
	}

	.story .story__article{
		padding: 48px 27px 48px 64px;
		width: 70%;
	}
	.story__title{
		line-height: 20px;
	}

	.facts{
		.facts__content{
			margin-left: 0;
		}

		.facts__year{
			width: 100%;
			text-align: left;
		}

		.facts__content{
			margin-top: 74px;
			width: 100%;

			.facts__title{
				width: 100%;
			}
		}
	}

	.authors{
		padding: 147px 0;

		.authors__content, .authors__thumbnail{
			width: 100% !important;
		}

		.authors__content{
			margin-bottom: 24px;
		}
	}
}

// Extra large devices (large desktops)
// No media query since the extra-large breakpoint has no upper bound on its width