// Modules
@import "modules/variables";

// Partials
@import "partials/reset";
@import "partials/typography";
@import "partials/menu";

// Vendor
@import "vendor/slideout";

section{
	background: $main_bg;
}

section > .container{
	width: 1200px;
}

.row{
	justify-content: space-between;
}

.section__title{
	font-size: 35px;
	line-height: 51px;
	margin-bottom: 49px;
	letter-spacing: 0.12em;
}

.section__text{
	line-height: 26px;
}

.year{
	position: absolute;
	font-size: 80px;
	line-height: 98px;
	letter-spacing: 0.05em;
	font-weight: 700;
	color: $main_color;
}

// Hero
.header{
	background: url("../img/history-bg.png") no-repeat center top / cover;
	padding: 40px 0 212px 0;

	.offer{
		text-align: center;
		margin: 279px auto 0 auto;
		width: 631px;

		&__title{
			margin-bottom: 105px;
		}
	}
}

// Story
.story{
	padding-top: 273px;
	.story__article{
		color: $sec_text_color;
		background: #f5f5f5;
		width: 77.5%;
		margin-left: auto;
		margin-right: 72px;
		position: relative;

		padding: 109px 170px 109px 258px;

		.story__thumbnail{
			position: absolute;
		    left: -200px;
		    top: 50%;
		    transform: translate(0%, -50%);
		    transform-origin: left;
		    height: 90%;
		}

		.story__content{
			.story__title{
				color: $sec_text_color;
			}

			.story__text{
				margin-top: 41px;
			}
		}

		.story__year{
			right: 80px;
			bottom: -46px;
		}
	}
}

// Facts
.facts{
	padding-top: 413px;

	.img__wrap{
		position: relative;
	}

	&__year{
		color: #fff;
		bottom: -46px;
		left: 0;	
		text-align: center;
		width: 100%;
	}

	.facts__content{
		width: 587px;

		.facts__title{
			width: 376px;
		}
	}
}

.authors{
	padding-top: 347px;
	padding-bottom: 181px;



	&__text{
		width: 524px;	
	}
}

// Responsive
@import "partials/responsive/general-responsive";
@import "partials/responsive/history-responsive";