body,html {
  font-family: $main_font;
  color: $main_text_color;
  font-size: 14px;
  font-weight: 500;
  transition: all .3s ease;
}

a, h1,h2,h3,h4,h5,h6{
  color: #fff;
}

a, a:hover{
  text-decoration: none;
}

ul, li, p, body, h1, h2, h3,h4,h5,h6{
  margin: 0;
  padding: 0;
  display: block;
}

h2{
  font-size: 25px;
}

h3{
  font-size: 20px;
}

*, *::after, *::before{
  box-sizing: border-box;
}

button, input, textarea{
  border: none;
  cursor: pointer;
  outline: none;
}

img {
  max-width: 100%;
  width: auto;
}

.menu_bars{
  display: none;
  margin-left: auto;
}

.container{
  width: 1722px;
  margin: auto;
}

.offer{
  text-transform: uppercase;
}

.d-flex{
  display: flex;
}