// Plain text
body{
	font-family: $main_font;
	font-size: $main_font_size;
	line-height: 1.643em;
}

a, .btn{
	font-family: $main_font;
}

h1, h2, h3, h4, h5, h6{
	text-transform: uppercase;
}

// Headings
h1{
	font-size: 71px;
	font-weight: 700;
	line-height: 1.225em;
  	letter-spacing: 0.1em;
}

.offer__subtitle{
	font-weight: 500;
	font-size: 35px;
	line-height: 2.688em;
	letter-spacing: 0.05em;
}

h3{
	font-size: 25px;
	text-transform: uppercase;
	font-weight: 700;
	letter-spacing: 0.07em;
}

h4{
	font-size: 20px;
}

// Buttons
.btn{
	text-transform: uppercase;
	letter-spacing: 0.04em;
  	line-height: 1.375em;
  	font-size: 18px;
  	font-weight: 500;
}