#mob-nav {
  position: fixed;
  width: 100%;
  max-width: 375px;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 0;
  overflow-y: scroll;
  background: #222;
}

#mob-nav .close_btn {
  margin-top: 22px;
  margin-right: 28px;
  display: block;
  margin-left: auto;
}

#mob-nav .mob-nav__menu {
  margin-left: 40px;
  top: 20%;
  position: absolute;
}

#mob-nav .mob-lang {
  color: #fff;
  position: absolute;
  bottom: 10%;
  font-size: 16px;
  text-transform: uppercase;
  margin-left: 70px;
}

#mob-nav .mob-nav__menu__item {
  padding: 20px 0 10px 30px;
}

#mob-nav .mob-nav__menu__item a {
  color: #fff;
  font-size: 16px;
  letter-spacing: 0.04em;
  text-transform: uppercase;
}

.toggle-button {
  display: none;
}

.slideout-menu {
  position: fixed;
  top: 0;
  bottom: 0;
  width: 256px;
  min-height: 100vh;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  z-index: 0;
  display: none;
}

.slideout-menu-left {
  left: 0;
}

.slideout-menu-right {
  right: 0;
}

.slideout-panel {
  position: relative;
  z-index: 1;
  background-color: #fff;
  min-height: 100vh;
}

.slideout-open,
.slideout-open body,
.slideout-open .slideout-panel {
  overflow: hidden;
}

.slideout-open .slideout-menu {
  display: block;
}